import { Controller, Block } from '@ali/4ever-cangjie';
import { getRangesFromEditor } from './getRangesFromEditor';

export const getBlocksFromEditor = (controller: Controller) => {
  const blocks: Block[] = [];
  const { document } = controller.value;
  const selecttions = getRangesFromEditor(controller);

  selecttions.forEach((selecttion) => {
    document.getLeafBlocksAtRange(selecttion).forEach((block) => {
      blocks.push(block);
    });
  });

  return blocks;
};
